@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
.circle-button{border:.2rem solid var(--text-primary);color:var(--text-primary);background:var(--bg-primary);border-radius:50%;cursor:pointer;display:inline-flex;margin:.5rem;flex-direction:row;align-items:center;justify-content:center;padding:.5rem;transition:all .5s}.circle-button:hover{color:var(--bg-primary);background:var(--text-primary)}
.menu-toggle{position:fixed;top:50%;left:2rem;background-color:var(--bg-primary);-webkit-transform:translateY(-50%);transform:translateY(-50%);z-index:200}.menu-toggle .menu-text{font-weight:bold;line-height:.8rem}.menu-toggle .menu-burger{padding-left:.3rem}.menu-toggle .menu-burger .menu-burger-bar{display:block;width:1.2rem;height:.2rem;background:var(--text-primary);margin-bottom:.3rem;border-radius:1rem;transition:all .5s}.menu-toggle .menu-burger .menu-burger-bar:last-child{margin-bottom:0}.menu-toggle:hover .menu-burger-bar{background:var(--bg-primary)}.menu-active .menu-toggle .menu-burger-bar:nth-child(1){-webkit-transform:rotateZ(-45deg) translateY(0.15em);transform:rotateZ(-45deg) translateY(0.15em);width:1.2rem}.menu-active .menu-toggle .menu-burger-bar:nth-child(2){opacity:0}.menu-active .menu-toggle .menu-burger-bar:nth-child(3){-webkit-transform:rotateZ(45deg) translateY(-0.15em);transform:rotateZ(45deg) translateY(-0.15em);width:1.2rem}@media screen and (max-width: 768px){.menu-toggle{left:1rem}}
.menu-item{position:fixed;top:50%;left:2rem;background-color:var(--bg-primary);-webkit-transform:translateY(-50%);transform:translateY(-50%);visibility:hidden;opacity:0;z-index:100}.menu-active .menu-item{visibility:visible;opacity:1}
.menu .menu-backdrop{display:none}.menu-active .menu-backdrop{display:block;position:fixed;left:0;top:0;height:100vh;width:100vw;z-index:99}.menu-active .menu-data{z-index:100}
.top-section{display:flex;flex-direction:row;justify-content:center;align-items:center;height:100vh}.top-section .intro{text-align:right;width:40%;padding-right:4rem;border-right:1px solid var(--text-primary)}.top-section .intro h1{font-size:9rem;font-weight:bold;line-height:.9em;margin:0}.top-section .intro p{font-size:2rem;margin:0}.top-section .signature{text-align:left;width:60%;height:100%;padding-left:4rem}.top-section .signature svg{height:100%;width:100%}.top-section .signature svg #signature-path{-webkit-animation:drawSignature 5s linear forwards;animation:drawSignature 5s linear forwards}.top-section .social{position:absolute;bottom:3rem;margin:0 auto;text-align:center}@media screen and (max-width: 1300px){.top-section .intro{width:50%}.top-section .intro h1{font-size:7rem}}@media screen and (max-width: 768px){.top-section{flex-direction:column}.top-section .intro{text-align:center;border-bottom:1px solid var(--text-primary);border-right:none;width:auto;padding:0;padding-bottom:2rem}.top-section .intro h1{font-size:5rem}.top-section .signature{height:40%;padding:0;padding-top:2rem}}@-webkit-keyframes drawSignature{to{stroke-dashoffset:0}}@keyframes drawSignature{to{stroke-dashoffset:0}}
.section-wrapper{width:100%}.section-wrapper .section{padding:5rem;display:flex}.section-wrapper .section .section-header{font-size:3rem;padding-right:2rem;border-right:1px solid var(--text-primary);display:flex;align-items:flex-start}.section-wrapper .section .section-header .section-header-icon{padding:.625rem;padding-left:0;font-size:3rem;transition:all .5s}.section-wrapper .section .section-content{padding-left:2rem}.section-wrapper .section.section-reverse{flex-direction:row-reverse}.section-wrapper .section.section-reverse .section-header{border-right:none;padding-right:0;padding-left:2rem;border-left:1px solid var(--text-primary)}.section-wrapper .section.section-reverse .section-content{padding-left:0;padding-right:2rem;text-align:right}.section-wrapper .section.section-centered{align-items:center}@media screen and (max-width: 768px){.section-wrapper .section,.section-wrapper .section.section-reverse{padding:3rem;flex-direction:column}.section-wrapper .section .section-header,.section-wrapper .section.section-reverse .section-header{padding:0;padding-bottom:.7rem;border:none;border-bottom:1px solid var(--text-primary);text-align:center;justify-content:center}.section-wrapper .section .section-content,.section-wrapper .section.section-reverse .section-content{padding:0;text-align:center}}
.project-title{font-size:1.4rem}.project-links{text-align:right}.project-links .circle-button:last-child{margin-right:0}
.about-section h4{font-size:2rem}.about-section .programming-icons svg{margin-left:2rem;width:3.5rem;height:3.5rem}.about-section .programming-icons svg:first-child{margin-left:0}
.footer{text-align:center}
.free-palestine{position:fixed;top:0;left:0;height:100%;width:100%;display:flex;align-items:center;justify-content:center;background:var(--bg-primary);z-index:999}.free-palestine.is-hiding{opacity:0;transition:1s}.free-palestine.is-hiding .free-palestine-content{-webkit-transform:translateY(-10em);transform:translateY(-10em);transition:1s}.free-palestine.is-hiding .to-website{-webkit-transform:translateX(5em);transform:translateX(5em);transition:1s}.free-palestine.hidden{display:none}.free-palestine-content{margin-right:10rem;text-align:center}.free-palestine-content h1{text-transform:uppercase;font-size:5vw;line-height:6vw;-webkit-animation:freePalestine 20s infinite ease-in-out;animation:freePalestine 20s infinite ease-in-out}.free-palestine-content h1 span{font-size:10vw}.free-palestine .to-website svg{font-size:2.5rem}@media screen and (max-width: 768px){.free-palestine{flex-direction:column}.free-palestine-content{margin:0;margin-bottom:3rem}}@-webkit-keyframes freePalestine{0%,100%{-webkit-transform:translateY(0.2em);transform:translateY(0.2em)}50%{-webkit-transform:translateY(-0.2em);transform:translateY(-0.2em)}}@keyframes freePalestine{0%,100%{-webkit-transform:translateY(0.2em);transform:translateY(0.2em)}50%{-webkit-transform:translateY(-0.2em);transform:translateY(-0.2em)}}
:root{--bg-primary: #fafafa;--text-primary: #000000;--text-secondary: #e0e0e0;font-size:16px}.dark-mode{--bg-primary: #2f2f2f;--text-primary: #ffffff;--text-secondary: #e0e0e0}body{padding:0;margin:0;background:#fafafa;background:var(--bg-primary);color:#000000;color:var(--text-primary);font-family:"Varela Round",sans-serif;font-size:16px;transition:all .2s}input,textarea,button{font-family:inherit}.body-wrapper{width:1100px;max-width:90%;margin:0 auto}a{color:#000000;color:var(--text-primary);text-decoration:none}a:hover{text-decoration:underline}.MuiTooltip-tooltip{font-size:1em !important}::-webkit-scrollbar{width:.3rem}::-webkit-scrollbar-track{background:transparent}::-webkit-scrollbar-thumb{background:#000000;background:var(--text-primary);border-radius:.5rem}::-webkit-scrollbar-thumb:hover{background:#e0e0e0;background:var(--text-secondary)}
